<template>
  <my-breadcrumb firstName="" secondName=""></my-breadcrumb>
  <el-card>
    <el-row>
      <el-col :span="8">
        <el-input
          placeholder=""
          v-model="queryInfo.query"
          clearable
          @clear="getOrderList"
          @keyup.enter="getOrderList"
        >
          <template #append>
            <el-button icon="el-icon-search" @click="getOrderList"></el-button>
          </template>
        </el-input>
      </el-col>
    </el-row>
    <el-table :data="orderList" border striped style="width: 100%">
      <el-table-column type="index" label="#"> </el-table-column>
      <el-table-column prop="order_number" label=""> </el-table-column>
      <el-table-column prop="order_price" label="" width="100px">
      </el-table-column>
      <el-table-column label="" width="100px">
        <template #default="scope">
          <el-tag type="success" v-if="scope.row.pay_status === '1'"
            ></el-tag
          >
          <el-tag type="danger" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="is_send" label="" width="100px">
      </el-table-column>
      <el-table-column label="" width="120px">
        <template #default="scope">
          {{ format(scope.row.update_time) }}
        </template>
      </el-table-column>
      <el-table-column label="" width="200px">
        <template #default="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="addressVisible = true"
          ></el-button>
          <el-button
            type="success"
            icon="el-icon-location"
            size="mini"
            @click="showExpressDetail(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      border
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="queryInfo.pagenum"
      :page-sizes="[5, 10, 15]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </el-card>
  <!--  -->
  <el-dialog
    title=""
    v-model="addressVisible"
    width="50%"
    @close="cancelEditAddr"
  >
    <el-form
      :model="addrForm"
      :rules="addrRules"
      label-width="100px"
      ref="addrFormRef"
    >
      <el-form-item label="/" prop="addr1">
        <el-cascader
          v-model="selectedVal"
          :options="cityData"
          :props="cityProps"
        ></el-cascader>
      </el-form-item>
      <el-form-item label="" prop="addr2">
        <el-input v-model="addrForm.addr2"></el-input>
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button @click="cancelEditAddr"> </el-button>
        <el-button type="primary" @click="cancelEditAddr"> </el-button>
      </span>
    </template>
  </el-dialog>
  <!--  -->
  <el-dialog title="" v-model="expressVisible" width="50%">
    <!-- <el-timeline>
      <el-timeline-item
        v-for="(activity, index) in activities"
        :key="index"
        :timestamp="activity.timestamp"
      >
        {{ activity.content }}
      </el-timeline-item>
    </el-timeline> -->
  </el-dialog>
</template>

<script>
import MyBreadcrumb from '@/components/MyBreadcrumb'
import { onMounted, reactive, ref, toRefs } from 'vue'
import { formatDate } from '@/utils/viewTools'
import { addrRules } from '@/utils/validateRules'
import { getOrdersRequest, getExpressRequest } from '@/api/order'
import cityData from '@/utils/cityData'
export default {
  name: 'Orders',
  components: {
    MyBreadcrumb
  },
  setup () {
    const addrFormRef = ref('')
    const state = reactive({
      orderList: [],
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      total: 0,
      addressVisible: false,
      addrForm: {
        addr1: [],
        addr2: ''
      },
      addrRules,
      selectedVal: [],
      cityData,
      cityProps: {
        expandTrigger: 'hover'
      },
      // 
      expressVisible: false,
      expressList: []
    })

    // 
    const cancelEditAddr = () => {
      state.addressVisible = false
      state.selectedVal = []
      state.addrForm = {}
    }
    const showExpressDetail = (row) => {
      getExpressList()
    }
    const closeExpressDialog = () => {
      state.expressVisible = false
      state.expressList = []
    }
    // 
    const handleSizeChange = (newSize) => {
      state.queryInfo.pagesize = newSize
      getOrderList()
    }
    const handleCurrentChange = (newPage) => {
      state.queryInfo.pagenum = newPage
      getOrderList()
    }
    // 
    const getExpressList = async () => {
      await getExpressRequest()
    }
    const getOrderList = async () => {
      const { orderList, total } = await getOrdersRequest(state.queryInfo)
      state.orderList = orderList
      state.total = total
      console.log('orderList', state.orderList);
    }
    const format = (dateTime) => {
      return formatDate(dateTime)
    }

    onMounted(() => {
      getOrderList()
    })
    return {
      ...toRefs(state),
      addrFormRef,
      format,
      handleSizeChange,
      handleCurrentChange,
      getOrderList,
      getExpressList,
      cancelEditAddr,
      showExpressDetail,
      closeExpressDialog
    }
  }
}
</script>

<style lang="less" scoped>
</style>